* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 2rem;
}

h1{
  font-size: 4rem;
}

.app {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color:  rgba(0, 0, 0, 0.4);
  color: white
}

.app:before{
  content: '';
  background: url('./assets//option1.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.app .search{
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
}

.container {
  max-width: 100vw;
  height: 70vh;
  margin: auto;
  padding: 0 1rem;
  position:relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.app.top{
  width: 100%;
  margin: 1rem auto;
}

.top{
  display: flex;
  text-align: center;
justify-content:space-around;
}
.app .description {
  right: -90%;
  transform-origin: 2 0;
  transform: rotate(269deg);
}

.name{
  color: rgb(106, 153, 162);
  font-family: cursive;
  font-size: 4vw;
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.12);
}

::placeholder{
  color: rgba(255, 255, 255, 0.648);
}

.bold{
font-weight: 600;
}